<template>
  <button type="button" @click="getRoute">getRoute</button>
  <button type="button" @click="push">Push</button>
  <button type="button" @click="replace">Replace</button>
  <button type="button" @click="go">Go</button>
  <hr />
  <button type="button" @click="addRoute">新增路由</button>
</template>

<script>
export default {
  methods: {
    // 包含歷史紀錄
    // push () {
    //   this.$router.push('/newpage/dynamicRouter/fafacb87500feff3') // 動態路由參數
    // },
    push () {
      this.$router.push({
        name: 'About'
      }) // 用 name 方式代入值，頁面切換至 About
    },
    // 沒有歷史紀錄
    replace () {
      this.$router.replace({
        name: 'About'
      }) // 用 name 方式代入值，頁面切換至 About
    },
    // 操作歷史記錄
    go () {
      this.$router.go(-1) // 代入正整數(往下一頁) or 負值數(倒退至上一頁)
    },
    // 取得常用參數
    getRoute () {
      // 取得路由的屬性
      // https://next.router.vuejs.org/zh/api/#routelocationnormalized
      // 範例: this.$route.fullPath (目前網址)
      // console.log(this.$route) // 取得目前路由資訊

      // 呼叫路由的方法
      // 參考: https://next.router.vuejs.org/zh/api/#addroute
      // 範例 : this.$router.go(-1) (回到前一頁)
      // console.log(this.$router)
    },
    // 延伸介紹(新增路由)
    addRoute () {
      this.$router.addRoute({
        path: '/newabout',
        name: 'newAbout',
        component: () => import('./About.vue')
      })
    }
  }
}
</script>
